.profile__name {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 373px;
  /*border: 1px solid teal;*/
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
}

@media screen and (max-width: 540px) {
  .profile__name  {
    max-width: 196px;
    font-size: 22px;
    line-height: 27px;
    margin-right: 10px;
  }
}


